@import url('https://fonts.googleapis.com/css2?family=Lato&family=Noto+Sans&family=Poppins&family=Source+Code+Pro&display=swap');

.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
    background-color: #C0DEE2;
    height: 100%;
    width: 100%;
}

.overlay-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Source Code Pro', monospace;
    font-size: 25px;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    max-width: 300px;
}

.overlay-loading-tip {
    text-transform: uppercase;
}

.user-select-none {
    user-select: none;
}

.start-button {
    font-family: 'Source Code Pro', monospace;
    font-size: 20px;
    padding: 12px 40px;
    border-radius: 5px;
    color: #fff;
    background-color: #5A7A96;
}

.start-button:hover {
    background-color: #6c8296;
}

.user-select-pointer:hover {
    cursor: pointer;
}

.element-visible {
    opacity: 1;
    transition: opacity 1s 1s;
}

.element-invisible {
    opacity: 0;
    transition: opacity 1s;
}

progress[value] {
    display: block;
    position: relative;
    appearance: none;
    width: 80%;
    height: 10px;
}

progress[value]::-webkit-progress-value {
    background-color: #5A7A96;
}

progress[value]::-moz-progress-bar {
    background-color: #5A7A96;
}