@import url('https://fonts.googleapis.com/css2?family=Lato&family=Noto+Sans&family=Poppins&family=Source+Code+Pro&display=swap');

.zone-overlay {
    user-select: none;
    width: 100vw;
    font-family: 'Source Code Pro', monospace;
    text-align: center;
    opacity: 0.95;
}

.zone-text {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.zone-title-container {
    padding-top: 50px;
    padding-left: 40px;
    position: relative;
}

.zone-title {
    position: absolute;
    color: #fff;
    font-size: 16px;
    background-color: #5A7A96;
    border-radius: 5px;
    width: 250px;
    padding: 10px 0;
}

.fadeOut {
    opacity: 0;
    transition: opacity 0.5s;
}

.fadeIn {
    opacity: 1;
    transition: opacity 0.5s 0.5s;
}